import { DropdownItem } from '@/types';

export default Vue.extend({
    name: 'Dropdown',
    props: {
        /*
            **íƒ€ìž…ìŠ¤í¬ë¦½íŠ¸ì—ì„œ ê°ì²´ í˜•íƒœë¡œ ì»´í¬ë„ŒíŠ¸ë¥¼ ì •ì˜í•  ë•Œ props ì˜ íƒ€ìž…ìœ¼ë¡œ íƒ€ìž…ìŠ¤í¬ë¦½íŠ¸ì˜ íƒ€ìž…ì„ ì‚¬ìš©í•  ìˆ˜ ì—†ë‹¤ëŠ” ì´ ì¹˜ëª…ì ì¸ ë‹¨ì ì€ í˜„ìž¬ í•´ë‹µì„ ì°¾ì§€ ëª»í–ˆë‹¤.** 
            ì§„í–‰ì¤‘ì¸ í”„ë¡œì íŠ¸ì—ì„œëŠ” ë§ë‹¨ ë² ì´ìŠ¤ ì»´í¬ë„ŒíŠ¸ì— í•´ë‹¹í•˜ëŠ” ì»´í¬ë„ŒíŠ¸ë“¤ë§Œ ê°ì²´ í˜•íƒœë¡œ ì»´í¬ë„ŒíŠ¸ë¥¼ ì •ì˜í•˜ê³  ìžˆëŠ”ë° ì´ ë¬¸ì œë¡œ íƒ€ìž…ì„ ì‚¬ìš©í•˜ì§€ ì•Šê³  ìžë°”ìŠ¤í¬ë¦½íŠ¸ ê¸°ë³¸ íƒ€ìž…ìœ¼ë¡œ í’€ì–´ì„œ ë°›ëŠ”ë‹¤.
            https://ui.toast.com/weekly-pick/ko_20190327
        */
        isMulti: { type: Boolean, default: true },
        items: { type: Array, required: true },
    },
    data() {
        return {
            keyword: '',
            parsedItems: [] as DropdownItem[],
        };
    },
    computed: {
        liveSearch(): DropdownItem[] {
            const items = this.items as DropdownItem[];
            return items.filter(
                (item) =>
                    item.label.toLowerCase().indexOf(this.keyword.trim()) > -1
            );
        },
        dropdownLabel(): string {
            const items = this.items as DropdownItem[];
            const checkedItems = items.filter(({ checked }) => checked);
            return checkedItems.length > 0
                ? this.ellipsis(
                      checkedItems.map((item) => item.label).join(',')
                  )
                : this.isMulti
                ? 'Select items'
                : 'Select an item';
        },
        isAllSelectable(): boolean {
            return (
                this.liveSearch.filter(({ disabled }) => disabled).length !==
                this.liveSearch.length
            );
        },
        isSelectAll(): boolean {
            return (
                this.liveSearch.filter((item) => !item.disabled).length ===
                this.liveSearch.filter((item) => item.checked).length
            );
        },
    },
    created() {
        this.parsedItems = this.items.concat() as DropdownItem[];
    },
    methods: {
        toggleItem(id: number): void {
            const item = this.parsedItems.find((item) => item.id === id);
            if (item?.disabled) return;

            if (this.isMulti) {
                this.parsedItems = this.parsedItems.map((item) =>
                    item.id === id ? { ...item, checked: !item.checked } : item
                );
            } else {
                // ê¸°ì¡´ ì•„ì´í…œì´ ì²´í¬ëœ ìƒíƒœì¸ì§€ í™•ì¸í•˜ê¸°
                const checked = this.parsedItems.find((item) => item.id === id)
                    ?.checked;
                if (checked) {
                    // ê¸°ì¡´ ì•„ì´í…œì´ ì²´í¬ë˜ì–´ ìžˆìœ¼ë©´ ì²´í¬ë¥¼ ëª¨ë‘ í•´ì œí•˜ê³ 
                    this.parsedItems = this.parsedItems.map((item) => ({
                        ...item,
                        checked: false,
                    }));
                } else {
                    // ê¸°ì¡´ ì•„ì´í…œì´ ì²´í¬ë˜ì–´ ìžˆì§€ ì•Šìœ¼ë©´ ë‚˜ë¨¸ì§€ ì•„ì´í…œì˜ ì²´í¬ë¥¼ ì·¨ì†Œí•˜ê³  í•´ë‹¹ ì•„ì´í…œë§Œ ì²´í¬í•œë‹¤.
                    this.parsedItems = this.parsedItems.map((item) => ({
                        ...item,
                        checked: item.id === id,
                    }));
                }
            }
            this.doUpdate(this.parsedItems);
        },
        ellipsis(label: string): string {
            const maxLength = 30;
            if (label.length > maxLength) {
                return label.substring(0, maxLength) + '...';
            }
            return label;
        },
        selectMulticheckableAll(): void {
            // ì „ì²´ ì„ íƒí•˜ê¸°
            this.parsedItems = this.parsedItems.map((item) =>
                item.disabled ? item : { ...item, checked: !this.isSelectAll }
            );
            this.doUpdate(this.parsedItems);
        },
        doUpdate(items: DropdownItem[]): void {
            this.$emit('update', items);
        },
        clearKeyword() {
            this.keyword = '';
        },
    },
});
